import React from "react";

import Logo from "../assets/img/logo.png";
import UkrFlag from "../assets/img/icons/ukraine-flag.png";
import { useTranslation } from "react-i18next";

function Footer() {
	const { t, i18n } = useTranslation();

	return (
		<footer className="footer _container-footer">
			<div className="footer__content">
				<img src={Logo} alt="Wortebuch" className="footer__logo" />
				<div className="footer__beta">
					{" "}
					<img src={UkrFlag} alt="Ukraine" /> {t("footer-beta")}
				</div>
				<div className="footer__right">
					© 2024 Wortebuch. Created by&nbsp;
					<a
						rel="noreferrer"
						target="_blank"
						href="https://www.instagram.com/denisdevelop?igsh=MWt6amgxd2p5Z3RyOQ=="
					>
						Denis Dev
					</a>
				</div>
			</div>
		</footer>
	);
}

export default Footer;
