import React, { useEffect, useRef, useState } from "react";
import { useTranslation } from "react-i18next";

import {
	Box,
	Button,
	ClickAwayListener,
	IconButton,
	Modal,
	TextField,
	Tooltip,
} from "@mui/material";
import RemoveCircleOutlineIcon from "@mui/icons-material/RemoveCircleOutline";
import DoNotDisturbOnIcon from "@mui/icons-material/DoNotDisturbOn";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import EditIcon from "@mui/icons-material/Edit";
import CloseIcon from "@mui/icons-material/Close";
import SpellcheckIcon from "@mui/icons-material/Spellcheck";
import DeleteForeverIcon from "@mui/icons-material/DeleteForever";
import MoreVertIcon from "@mui/icons-material/MoreVert";

import CircularProgressUI from "./UI/Loader/CircularProgress/CircularProgress";
import ModalMore from "./ModalMore/ModalMore";

function Words({ words, setWords }) {
	const [learnedWords, setLearnedWords] = useState([]);
	const [open, setOpen] = useState(false);
	const [openStatistics, setOpenStatistics] = useState(false);
	const [statistics, setStatistics] = useState({});

	const [editWord, setEditWord] = useState("");
	const [editTranslate, setEditTranslate] = useState("");

	const [oldWord, setOldWord] = useState("");
	const [oldTranslate, setOldTranslate] = useState("");
	const { t, i18n } = useTranslation();

	const [openToolTip, setOpenToolTip] = useState(false);

	useEffect(() => {
		setLearnedWords(JSON.parse(localStorage.getItem("learned-words")) || []);
	}, []);

	function removeWord(word) {
		// Получаем массив из localStorage
		const words = JSON.parse(localStorage.getItem("words")) || [];

		// Фильтруем массив, удаляя объект с нужным ключом
		const updatedWords = words.filter(item => {
			return item.word !== word;
		});
		console.log(updatedWords);
		console.log(word);

		// Сохраняем обновленный массив обратно в localStorage
		localStorage.setItem("words", JSON.stringify(updatedWords));
		setWords(updatedWords);
	}
	function removeGelearnteWord(word) {
		// Получаем массив из localStorage
		const words = JSON.parse(localStorage.getItem("learned-words")) || [];

		// Фильтруем массив, удаляя объект с нужным ключом
		const updatedWords = words.filter(item => {
			const key = Object.keys(item)[0];
			return key !== word;
		});

		// Сохраняем обновленный массив обратно в localStorage
		localStorage.setItem("learned-words", JSON.stringify(updatedWords));
		setLearnedWords(updatedWords);
	}
	function learnedWord(word, translate) {
		const learnedWord = JSON.parse(localStorage.getItem("learned-words")) || [];

		if (learnedWord.length) {
			learnedWord.push({ [word]: translate });
			localStorage.setItem("learned-words", JSON.stringify(learnedWord));
			setLearnedWords(learnedWord);
		} else {
			localStorage.setItem(
				"learned-words",
				JSON.stringify([{ [word]: translate }])
			);
			setLearnedWords([{ [word]: translate }]);
		}

		removeWord(word);
	}

	const handleOpen = () => {
		setOpen(true);
	};
	const handleClose = () => {
		setOpen(false);
	};

	const handleOpenStatistics = (e, obj) => {
		setOpenStatistics(true);
		setStatistics(obj);
	};
	const handleCloseStatistics = () => {
		setOpenStatistics(false);
	};

	function handleEditWord(word, translate) {
		handleOpen();

		setOldWord(word);
		setOldTranslate(translate);
		setEditWord(word);
		setEditTranslate(translate);
	}
	function updateArray(array, wort, translate, editWord, editTranslate) {
		return array.map(item => {
			let updatedItem = { ...item };

			// Изменяем ключ "word", если он совпадает с `wort`
			if (item.word === wort) {
				updatedItem.word = editWord;
			}

			// Изменяем значение "translate", если оно совпадает с `translate`
			if (item.translate === translate) {
				updatedItem.translate = editTranslate;
			}

			return updatedItem;
		});
	}
	function saveEdit() {
		const newArr = updateArray(
			words,
			oldWord,
			oldTranslate,
			editWord,
			editTranslate
		);

		localStorage.setItem("words", JSON.stringify(newArr));
		setWords(newArr);
		setEditTranslate("");
		setEditWord("");

		handleClose();
	}
	function copyAllWords(array) {
		// Преобразуем массив объектов в строки формата "keyObjekt - valueObjekt"
		const keyValuePairs = array.map(obj => {
			const key = Object.keys(obj)[0];
			const value = obj[key];
			return `${key} - ${value}`;
		});

		// Объединяем строки в одну строку, разделенную новыми строками
		const clipboardText = keyValuePairs.join("\n");

		// Создаем временной элемент textarea
		const textarea = document.createElement("textarea");
		textarea.value = clipboardText;

		// Добавляем textarea в документ
		document.body.appendChild(textarea);

		// Выделяем и копируем текст
		textarea.select();
		document.execCommand("copy");

		// Удаляем textarea из документа
		document.body.removeChild(textarea);
	}
	const handleTooltipClose = () => {
		setOpenToolTip(false);
	};
	const handleTooltipOpen = () => {
		setOpenToolTip(true);

		setTimeout(() => {
			handleTooltipClose();
		}, 5000);
	};

	return (
		<>
			<div className="words__header">
				<Tooltip
					PopperProps={{
						disablePortal: true,
					}}
					disableFocusListener
					disableHoverListener
					disableTouchListener
					onClose={handleTooltipClose}
					open={openToolTip}
					title={t("words-chatgpt")}
				>
					<Button
						onClick={() => {
							handleTooltipOpen();
							copyAllWords(words);
						}}
						startIcon={<ContentCopyIcon />}
						className="words__copy"
						sx={{ marginBottom: "30px" }}
					>
						{t("words-copy")}
					</Button>
				</Tooltip>
			</div>
			<div className="words">
				{words.map(item => {
					return (
						<div className="words__content" key={item.word}>
							<div className="words__main">
								<>
									<h2>
										{item.word} &nbsp;
										<IconButton
											onClick={() => handleEditWord(item.word, item.translate)}
											aria-label="edit"
										>
											<EditIcon />
										</IconButton>
									</h2>
									<h3>{item.translate}</h3>
								</>
							</div>
							<Modal
								open={open}
								onClose={handleClose}
								aria-labelledby="child-modal-title"
								aria-describedby="child-modal-description"
								BackdropProps={{
									style: {
										backgroundColor: "rgba(0, 0, 0, 0.3)",
										boxShadow: "none",
									},
								}}
							>
								<Box className="modal" sx={{ padding: "20px 20px" }}>
									<div className="modal__header">
										<IconButton aria-label="clsoe" onClick={handleClose}>
											<CloseIcon />
										</IconButton>
									</div>
									<div className="modal__body">
										<TextField
											id="edit-word"
											label={t("input-placeholder-word")}
											variant="standard"
											value={editWord}
											onChange={e => setEditWord(e.target.value)}
											sx={{ maxWidth: 500, width: "100%" }}
										/>
										<TextField
											id="edit-translate"
											label={t("input-placeholder-translate")}
											variant="standard"
											value={editTranslate}
											onChange={e => setEditTranslate(e.target.value)}
											sx={{ maxWidth: 500, width: "100%" }}
										/>
									</div>
									<Button startIcon={<EditIcon />} onClick={saveEdit}>
										{t("modal-confirm-btn")}
									</Button>
								</Box>
							</Modal>
							<ModalMore
								open={openStatistics}
								closeModal={handleCloseStatistics}
								statistics={statistics}
								removeWord={removeWord}
								learnedWord={learnedWord}
							/>
							<div className="words__right">
								{item.statistics ? (
									<div className="words__statistics">
										<div className="words__count-learned">
											<IconButton onClick={e => handleOpenStatistics(e, item)}>
												<MoreVertIcon />
											</IconButton>
											<CircularProgressUI
												value={item.statistics.countLernen * 10}
											/>
										</div>
									</div>
								) : (
									""
								)}

								<div className="words__btn-group">
									{window.innerWidth >= 768 ? (
										<>
											<div className="words__delete ">
												<Button
													startIcon={<DeleteForeverIcon />}
													onClick={() => {
														removeWord(item.word);
													}}
												>
													{t("delete-word-btn")}
												</Button>
											</div>
											<div className="words__learned">
												<Button
													startIcon={<CheckCircleIcon />}
													onClick={() => learnedWord(item.word, item.translate)}
												>
													{t("learned-word-btn")}
												</Button>
											</div>
										</>
									) : (
										<div className="words__learned">
											<Button
												startIcon={<CheckCircleIcon />}
												onClick={() => learnedWord(item.word, item.translate)}
											>
												{t("learned-word-btn")}
											</Button>
										</div>
									)}
								</div>
							</div>
						</div>
					);
				})}

				<h1 className="words__title">
					<SpellcheckIcon />
					&nbsp;&nbsp;
					{t("word-title")}
				</h1>
				{learnedWords.length ? (
					learnedWords.map(item => (
						<>
							<div className="words__content" key={Object.keys(item)[0]}>
								<div className="words__main">
									<h2>{Object.keys(item)[0]}</h2>
									<h3>{Object.values(item)[0]}</h3>
								</div>
								<div className="words__right">
									<div className="words__delete">
										<Button
											startIcon={<RemoveCircleOutlineIcon />}
											onClick={() => removeGelearnteWord(Object.keys(item)[0])}
										>
											{t("delete-word-btn")}
										</Button>
									</div>
								</div>
							</div>
						</>
					))
				) : (
					<div className="words__no-words">
						<DoNotDisturbOnIcon />
						{t("no-words-text")}
					</div>
				)}
			</div>
		</>
	);
}

export default Words;
