import React, { useState } from "react";
import { useTranslation } from "react-i18next";

import { Button, TextField } from "@mui/material";
import formatDate from "../utils/formatDate";

function AddWord() {
	const [word, setWord] = useState("");
	const [translate, setTranslate] = useState("");

	const [added, setAdded] = useState(false);
	const { t, i18n } = useTranslation();

	const handleWord = e => {
		setWord(e.target.value);
	};

	const handleTranslate = e => {
		setTranslate(e.target.value);
	};
	const addWord = () => {
		const words = JSON.parse(localStorage.getItem("words"));

		if (!word || !translate) {
			return false;
		}

		if (words) {
			words.unshift({
				word: word,
				translate: translate,
				category: "",
				statistics: {
					countLernen: 0,
					dateAdded: formatDate(new Date()),
				},
			});
			localStorage.setItem("words", JSON.stringify(words));
		} else {
			localStorage.setItem(
				"words",
				JSON.stringify([
					{
						word: word,
						translate: translate,
						category: "",

						statistics: {
							countLernen: 0,
							dateAdded: formatDate(new Date()),
						},
					},
				])
			);
		}

		setWord("");
		setTranslate("");
		setAdded(true);

		setTimeout(() => {
			setAdded(false);
		}, 3000);
	};

	return (
		<main className="main">
			<div className="_container">
				<section className="add-word">
					<div className="add-word__content">
						<div className="add-word__title">{t("add-word-title")} </div>
						<TextField
							onInput={handleWord}
							value={word}
							fullWidth
							id="word"
							label={t("input-placeholder-word")}
							variant="standard"
						/>
						<TextField
							onInput={handleTranslate}
							value={translate}
							fullWidth
							id="Translate"
							label={t("input-placeholder-translate")}
							variant="standard"
						/>
						<Button onClick={addWord} variant="contained">
							{t("add-word-btn")}
						</Button>
						{added ? (
							<p className="add-word__text">{t("add-word-text")}</p>
						) : (
							""
						)}
					</div>
				</section>
			</div>
		</main>
	);
}

export default AddWord;
