import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";

import Words from "../components/Words";
import formatDate from "../utils/formatDate";

function Home() {
	const [words, setWords] = useState(JSON.parse(localStorage.getItem("words")));

	const { t } = useTranslation();

	const setInitArr = () => {
		const words = JSON.parse(localStorage.getItem("words"))?.length;
		const date = formatDate(new Date());

		const initiallyArr = [
			{
				word: "schlecht",
				translate: "плохо",
				category: "",
				statistics: {
					countLernen: 0,
					countMistake: 0,
					dateAdded: date,
				},
			},
			{
				word: "fuchs",
				translate: "рыжий",
				category: "",
				statistics: {
					countLernen: 0,
					countMistake: 0,
					dateAdded: date,
				},
			},
			{
				word: "denken",
				translate: "думать",
				category: "",
				statistics: {
					countLernen: 0,
					countMistake: 0,
					dateAdded: date,
				},
			},
			{
				word: "angenehm",
				translate: "приятно",
				category: "",
				statistics: {
					countLernen: 0,
					countMistake: 0,
					dateAdded: date,
				},
			},
			{
				word: "das Essen",
				translate: "еда",
				category: "",
				statistics: {
					countLernen: 0,
					countMistake: 0,
					dateAdded: date,
				},
			},
		];

		if (!words) {
			localStorage.setItem("words", JSON.stringify(initiallyArr));

			setWords(initiallyArr);
		}
	};

	useEffect(() => {
		setInitArr();
	}, []);

	return (
		<main className="_container main">
			<section className="hero">
				{!words ? (
					t("no-words-home")
				) : (
					<Words words={words} setWords={setWords} />
				)}
			</section>
		</main>
	);
}

export default Home;
