function generateUniqueRandomIndexes(
	countWords,
	countIndexes,
	excludedIndex = null
) {
	const indexes = new Set();

	while (indexes.size < countIndexes) {
		let randomIndex;
		do {
			randomIndex = Math.floor(Math.random() * countWords);
		} while (randomIndex === excludedIndex || indexes.has(randomIndex));

		indexes.add(randomIndex);
	}

	return Array.from(indexes);
}

export default generateUniqueRandomIndexes;
