import classNames from "classnames";
import React, { useEffect, useRef } from "react";
import { useState } from "react";

import { NavLink, Link } from "react-router-dom";

import Logo from "../assets/img/logo.png";
import { useTranslation } from "react-i18next";
import { FormControl, InputLabel, MenuItem, Select } from "@mui/material";

function Header() {
	const [isBurgerOpen, setIsBurgerOpen] = useState(false);
	const burgerElem = useRef(null);
	const burgerMenuClasses = classNames(
		"burger-menu",
		isBurgerOpen ? "_active" : ""
	);
	const { t, i18n } = useTranslation();

	const [language, setLanguage] = React.useState("de");

	const changeLanguage = lng => {
		i18n.changeLanguage(lng);
	};

	useEffect(() => {
		if (
			i18n.language !== "de" &&
			i18n.language !== "pl" &&
			i18n.language !== "ua"
		) {
			setLanguage("de");
		} else {
			setLanguage(i18n.language);
		}
	}, []);

	const handleChange = event => {
		setLanguage(event.target.value);
		changeLanguage(event.target.value);
	};

	return (
		<>
			<header className="header _container">
				<div className="header__content">
					<Link to="/" className="header__logo-container ">
						<img src={Logo} alt="Wortshatz" className="header__logo-img" />
						<h1 className="header__logo">{t("wortebuch")}</h1>
					</Link>

					<nav className="header__nav nav-header">
						<ul className="nav-header__list">
							<li className="nav-header__item">
								<FormControl sx={{ width: "150px" }}>
									<InputLabel id="demo-simple-select-label">
										{t("change-language-btn")}
									</InputLabel>
									<Select
										labelId="demo-simple-select-label"
										id="demo-simple-select"
										value={language}
										label="Age"
										onChange={handleChange}
									>
										<MenuItem value={"de"}>Deutsch</MenuItem>
										<MenuItem value={"pl"}>Polska</MenuItem>
										<MenuItem value={"ua"}>Українська</MenuItem>
									</Select>
								</FormControl>
							</li>
							<li className="nav-header__item">
								<NavLink to="/" className="nav-header__link underline-anim">
									{t("my-word-nav")}
								</NavLink>
							</li>
							<li className="nav-header__item">
								<NavLink
									to="/add-word"
									className="nav-header__link underline-anim"
								>
									{t("add-word-nav")}
								</NavLink>
							</li>
							<li className="nav-header__item ">
								<NavLink
									to="/lernen"
									className="nav-header__link underline-anim"
								>
									{t("learn-nav")}
								</NavLink>
							</li>
						</ul>
					</nav>
				</div>
			</header>
			<div className="burger-menu-container">
				<Link to="/">
					<h1 className="header__logo">{t("wortebuch")}</h1>
				</Link>

				<input
					type="checkbox"
					id="burger-menu-toggle"
					onClick={() => setIsBurgerOpen(!isBurgerOpen)}
					ref={burgerElem}
				/>
				<label htmlFor="burger-menu-toggle" className="burger-icon">
					<span className="bar"></span>
					<span className="bar"></span>
					<span className="bar"></span>
				</label>
			</div>

			<div
				className={burgerMenuClasses}
				onClick={() => {
					burgerElem.current.checked = false;
					setIsBurgerOpen(false);
				}}
			>
				<nav className="burger-menu__nav">
					<ul className="burger-menu__list">
						<li className="burger-menu__item _logo">
							<img src={Logo} alt="Mein Rechte" className="header__logo-img" />
						</li>
						<li className="burger-menu__item ">
							<NavLink to="/" className="nav-header__link underline-anim">
								{t("my-word-nav")}
							</NavLink>
						</li>
						<li className="burger-menu__item">
							<NavLink
								to="/add-word"
								className="nav-header__link underline-anim"
							>
								{t("add-word-nav")}
							</NavLink>
						</li>
						<li className="burger-menu__item">
							<NavLink to="/lernen" className="nav-header__link underline-anim">
								{t("learn-nav")}
							</NavLink>
						</li>
						<li className="burger-menu__item">
							<FormControl sx={{ width: "150px", marginTop: "30px" }}>
								<InputLabel id="demo-simple-select-label">
									{t("change-language-btn")}
								</InputLabel>
								<Select
									labelId="demo-simple-select-label"
									id="demo-simple-select"
									value={language}
									label="language"
									onChange={handleChange}
								>
									<MenuItem value={"de"}>Deutsch</MenuItem>
									<MenuItem value={"pl"}>Polska</MenuItem>
									<MenuItem value={"ua"}>Українська</MenuItem>
								</Select>
							</FormControl>
						</li>
					</ul>
				</nav>
			</div>
		</>
	);
}

export default Header;
