import React from "react";
import { useEffect, useState } from "react";

import { useTranslation } from "react-i18next";
import { NavLink } from "react-router-dom";

import shuffleArr from "../utils/shuffleArr";
import generateUniqueRandomIndexes from "../utils/generateUniqueRandomIndexes";

import { Button } from "@mui/material";

function Lernen() {
	const words = JSON.parse(localStorage.getItem("words"));

	const wordsNative = words.map(item => item.word);
	const wordsTranslate = words.map(item => item.translate);
	const countWords = wordsNative.length;

	const [mainWord, setMainWord] = useState("");

	const [level, setLevel] = useState(0);
	const [finished, setFinisched] = useState(false);

	const [shuffledButtons, setShuffledButtons] = useState([]);
	const [randomNumbersNative, setRandomNumbersNative] = useState(
		generateUniqueRandomIndexes(countWords, countWords)
	);

	const [voices, setVoices] = useState([]);
	const { t, i18n } = useTranslation();

	const [buttonColor, setButtonColor] = useState("primary");

	useEffect(() => {
		setFinisched(false);
		initSpeaker();
	}, []);

	useEffect(() => {
		createLevel();
	}, [level]);

	useEffect(() => {
		if (countWords > 4) {
			handleSpeak(mainWord);
		}
	}, [mainWord]);

	const createLevel = () => {
		if (countWords > 4) {
			let rightTranslate = words[randomNumbersNative[level]]?.translate;

			if (!rightTranslate) {
				setFinisched(true);

				return false;
			}

			const randomNumbersTranslate = generateUniqueRandomIndexes(
				countWords,
				3,
				randomNumbersNative[level]
			);

			function incrementCountLernen(e, translate) {
				const words = JSON.parse(localStorage.getItem("words"));

				const arr = words.map(item => {
					if (item.translate === translate) {
						return {
							...item,
							statistics: {
								...item.statistics,
								countLernen: item.statistics.countLernen + 1,
							},
						};
					}
					return item;
				});

				localStorage.setItem("words", JSON.stringify(arr));

				return arr;
			}

			function incrementCountMistake(e, translate) {
				const words = JSON.parse(localStorage.getItem("words"));

				const arr = words.map(item => {
					if (item.translate === translate) {
						console.log(item.translate);

						return {
							...item,
							statistics: {
								...item.statistics,
								countMistake: item.statistics.countMistake + 1,
							},
						};
					}
					return item;
				});

				console.log(arr);

				localStorage.setItem("words", JSON.stringify(arr));

				return arr;
			}

			const wrongAnswer = (e, translate) => {
				const target = e.target;

				if (target) {
					target.classList.add("wrong");
					setTimeout(() => {
						target.classList.remove("wrong");
					}, 1500); // 1000 миллисекунд = 3 секунды
				}

				incrementCountMistake(words, translate);
			};

			const correctAnswer = (e, translate) => {
				const target = e.target;

				if (target) {
					target.classList.add("correct");
					setTimeout(() => {
						target.classList.remove("correct");
					}, 700); // 1000 миллисекунд = 3 секунды
				}

				incrementCountLernen(words, translate);
			};

			// Создаем массив кнопок как JSX элементы
			const buttons = [
				<Button
					onClick={e => {
						wrongAnswer(e, rightTranslate);
					}}
					color={buttonColor}
					fullWidth
					variant="contained"
					className="lernen__answer-button"
					key={1}
				>
					{wordsTranslate[randomNumbersTranslate[0]]}
				</Button>,
				<Button
					onClick={e => {
						wrongAnswer(e, rightTranslate);
					}}
					fullWidth
					variant="contained"
					className="lernen__answer-button"
					key={2}
				>
					{wordsTranslate[randomNumbersTranslate[1]]}
				</Button>,
				<Button
					onClick={e => {
						wrongAnswer(e, rightTranslate);
					}}
					fullWidth
					variant="contained"
					className="lernen__answer-button"
					key={3}
				>
					{wordsTranslate[randomNumbersTranslate[2]]}
				</Button>,
				<Button
					fullWidth
					variant="contained"
					className="lernen__answer-button"
					key={4}
					onClick={e => {
						correctAnswer(e, rightTranslate);
						setTimeout(() => {
							setLevel(level + 1);
						}, 800);
					}}
				>
					{rightTranslate}
				</Button>,
			];

			setShuffledButtons(shuffleArr(buttons));
		}

		setMainWord(wordsNative[randomNumbersNative[level]]);
	};
	const initSpeaker = () => {
		const synth = window.speechSynthesis;
		const voiceList = synth.getVoices();
		setVoices(voiceList);

		// Обновление списка голосов при их изменении
		synth.onvoiceschanged = () => {
			setVoices(synth.getVoices());
		};
	};
	const handleSpeak = word => {
		const utterance = new SpeechSynthesisUtterance(word);
		utterance.lang = "de-DE";
		window.speechSynthesis.speak(utterance);
	};

	return (
		<main className="main ">
			<div className="_container">
				<section className="lernen">
					<div className="lernen__content">
						<h2 className="lernen__title">{t("learne-title")}</h2>
						<div className="lernen__body">
							{finished ? (
								<div className="lernen__finished">
									<h2 className="lernen__finished-title">
										{t("learne-text-finish1")} <br />
										{t("learne-text-finish2")}
										<span>{countWords}</span>
										{t("learne-text-finish3")}
									</h2>
									<div className="lernen__finished-group">
										<Button variant="contained">
											<NavLink to="/" className="lernen__finished-nav">
												{t("home-link")}
											</NavLink>
										</Button>
										<Button variant="contained">
											<NavLink
												to="/lernen"
												className="lernen__finished-nav"
												onClick={() => window.location.reload()}
											>
												{t("learn-nav")}
											</NavLink>
										</Button>
									</div>
								</div>
							) : countWords > 4 ? (
								<>
									<div className="lernen__count-word">
										{level + 1} <span>/</span> {countWords}
									</div>
									<div className="lernen__word">{mainWord}</div>
									<div className="lernen__translates">
										{shuffledButtons.map((button, index) => (
											<div className="lernen__button" key={index}>
												{button}
											</div>
										))}
									</div>
								</>
							) : (
								<p>{t("must-5wort")}</p>
							)}
						</div>
					</div>
				</section>
			</div>
		</main>
	);
}

export default Lernen;
