import {
	Box,
	Button,
	IconButton,
	Modal,
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableHead,
	TableRow,
} from "@mui/material";
import Paper from "@mui/material/Paper";

import CloseIcon from "@mui/icons-material/Close";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import DeleteForeverIcon from "@mui/icons-material/DeleteForever";

import React from "react";
import { useTranslation } from "react-i18next";

function ModalMore({ closeModal, open, statistics, removeWord, learnedWord }) {
	const { t } = useTranslation();

	console.log(closeModal);
	return (
		<Modal
			open={open}
			onClose={closeModal}
			BackdropProps={{
				style: {
					backgroundColor: "rgba(0, 0, 0, 0.3)",
					boxShadow: "none",
				},
			}}
			aria-labelledby="child-modal-title"
			aria-describedby="child-modal-description"
		>
			<Box className="modal" sx={{ maxWidth: 800 }}>
				<div className="modal__header">
					<IconButton aria-label="clsoe" onClick={closeModal}>
						<CloseIcon />
					</IconButton>
				</div>
				<Box className="modal__body" sx={{ padding: "0 20px" }}>
					<TableContainer component={Paper}>
						<Table aria-label="simple table">
							<TableHead>
								<TableRow>
									<TableCell align="center">
										{t("words-statistics-word")}
									</TableCell>
									<TableCell align="center">
										{t("words-statistics-translation")}
									</TableCell>
									<TableCell align="center">
										{t("words-statistics-count-learned")}
									</TableCell>
									<TableCell align="center">
										{t("words-statistics-count-mistake")}
									</TableCell>
									<TableCell align="center">
										{t("words-statistics-date")}
									</TableCell>
								</TableRow>
							</TableHead>
							<TableBody>
								<TableRow
									key={statistics.word}
									sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
								>
									<TableCell align="center">{statistics?.word}</TableCell>
									<TableCell align="center">{statistics?.translate}</TableCell>
									<TableCell align="center">
										{statistics.statistics?.countLernen}
									</TableCell>
									<TableCell align="center">
										{statistics.statistics?.countMistake}
									</TableCell>
									<TableCell align="center">
										{statistics.statistics?.dateAdded}
									</TableCell>
								</TableRow>
							</TableBody>
						</Table>
					</TableContainer>
				</Box>
				<div className="modal__group-btn">
					<div className="words__delete modal__delete">
						<Button
							startIcon={<DeleteForeverIcon />}
							onClick={() => {
								removeWord(statistics.word);
								closeModal();
							}}
						>
							{t("delete-word-btn")}
						</Button>
					</div>
					<div className="words__learned">
						<Button
							startIcon={<CheckCircleIcon />}
							onClick={() => {
								learnedWord(statistics.word, statistics.translate);
								closeModal();
							}}
						>
							{t("learned-word-btn")}
						</Button>
					</div>
				</div>
			</Box>
		</Modal>
	);
}

export default ModalMore;
